* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  padding-top: 50px;
  font-family: ProximaNova, sans-serif;
  color: #333;
  background-color: #f2f2f2;
}

.container {
  max-width: 1170px;
  width: 100%;
  max-width: 1220px;
  padding: 0 25px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  max-height: 100%;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

@font-face {
  font-family: "ProximaNova";
  src: url("../fonts/ProximaNova-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "ProximaNova";
  src: url("../fonts/ProximaNova-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
.page {
  overflow: hidden;
}

.main-title {
  text-align: center;
}

.receipt {
  margin: 0 auto;
  max-width: 450px;
}
.receipt .receipt__item {
  width: 100%;
  border: 1px solid #999;
  background-color: #fcfcfc;
  box-shadow: 0 2px 5px #999;
  transition: box-shadow 0.35s ease;
}
.receipt .receipt__item:not(last-child) {
  margin-bottom: 10px;
}
.receipt .receipt__item:hover {
  box-shadow: 0 2px 5px #333;
}
.receipt .receipt__item--opened .receipt-header::before {
  transform: translateY(-50%) scale(1.2, -1);
}
.receipt .receipt__item--opened .receipt-header::after {
  width: 75%;
  opacity: 1;
  visibility: visible;
}
.receipt .receipt__item--opened > .receipt-details {
  display: block;
  padding: 10px 25px;
}
.receipt-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 10px 25px 10px 35px;
  cursor: pointer;
}
.receipt-header::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 10px;
  width: 10px;
  height: 10px;
  background-image: url("../img/down-chevron.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transform: translateY(-50%);
  transition: transform 0.35s ease;
}
.receipt-header::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 1px;
  background-color: #999;
  transform: translateX(-50%);
  transition: width 0.35s ease;
}
.receipt-header__date, .receipt-header__amount {
  position: relative;
}
.receipt-header__date::after, .receipt-header__amount::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 50%;
  height: 1px;
  width: 0;
  background-color: #999;
  transform: translateX(-50%);
  transition: width 0.35s ease;
}
.receipt-header:hover::before {
  transform: translateY(-50%) scale(1.2);
}
.receipt-header:hover .receipt-header__date::after,
.receipt-header:hover .receipt-header__amount::after {
  width: 100%;
}
.receipt-details {
  padding: 0 25px;
  font-size: 14px;
  display: none;
  transition: padding 0.35s ease;
}
.receipt .receipt-item__header {
  display: flex;
  justify-content: space-between;
}
.receipt .receipt-item__products > .products-amount {
  position: relative;
  display: inline-block;
  padding-left: 15px;
  cursor: pointer;
}
.receipt .receipt-item__products > .products-amount::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -2px;
  width: 0;
  height: 1px;
  background-color: #999;
  transform: translateX(-50%);
  transition: width 0.35s ease;
}
.receipt .receipt-item__products > .products-amount::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 8px;
  height: 8px;
  background-image: url("../img/down-chevron.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transform: translateY(-50%);
  transition: transform 0.35s ease;
}
.receipt .receipt-item__products > .products-amount:hover::after {
  width: 100%;
}
.receipt .receipt-item__products > .products-list {
  display: none;
}
.receipt .receipt-item__products > .products-list > .product-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.receipt .receipt-item__products > .products-list > .product-item > .product-item__image {
  flex: 0 0 75px;
  margin-right: 15px;
  height: 50px;
}
.receipt .receipt-item__products > .products-list > .product-item > .product-item__image img {
  display: block;
  margin: 0 auto;
}
.receipt .receipt-item__products > .products-list > .product-item > .product-item__descr {
  width: 100%;
  font-weight: 700;
  overflow: hidden;
}
.receipt .receipt-item__products > .products-list > .product-item > .product-item__descr .product-item__name {
  margin: 0 0 7px 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.receipt .receipt-item__products > .products-list > .product-item > .product-item__descr > .product-item__counters {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
}
.receipt .receipt-item__products > .products-list > .product-item > .product-item__descr > .product-item__counters > p {
  margin: 0;
}
.receipt .receipt-item__products--opened > .products-amount::before {
  transform: translateY(-50%) scale(1.2, -1);
}
.receipt .receipt-item__products--opened > .products-list {
  display: block;
}
@media (max-width: 576px) {
  .receipt .product-item {
    position: relative;
    flex-direction: column;
  }
  .receipt .product-item::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: -3px;
    width: 50%;
    height: 1px;
    background-color: #999;
    transform: translateX(-50%);
  }
  .receipt .product-item__image {
    margin-bottom: 15px;
    margin-right: 0 !important;
  }
  .receipt .receipt-item__products > .products-list > .product-item > .product-item__descr .product-item__counters {
    font-size: 16px;
  }
}
@media (max-width: 425px) {
  .receipt .receipt-item__header {
    flex-direction: column;
    text-align: center;
  }
  .receipt .receipt-item__header > p {
    margin: 3px 0;
  }
  .receipt .receipt-header {
    flex-direction: column;
    text-align: center;
  }
  .receipt .receipt-header > p {
    margin: 5px 0;
  }
  .receipt .receipt-item__products > .products-list > .product-item > .product-item__descr {
    text-align: center;
  }
  .receipt .receipt-item__products > .products-list > .product-item > .product-item__descr > .product-item__counters {
    flex-direction: column;
  }
  .receipt .receipt-item__products > .products-list > .product-item > .product-item__descr > .product-item__counters > p {
    margin-bottom: 5px;
  }
}